* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

html {
  font-size: 100%;
}

body {
  font-family: Roboto, sans-serif;
  font-size: .875rem;
  line-height: 1.5;
}

@media (prefers-color-scheme: dark) {
  body {
    color: #f5f5f5;
    background-color: #161515;
  }
}

button {
  cursor: pointer;
}

ul, ol {
  list-style: none;
}

a {
  color: #f5f5f5;
  text-decoration: none;
  transition: all .3s ease-in-out;
}

a:hover {
  color: #ff6b08;
}

svg {
  transition: all .3s ease-in-out;
}

button {
  background: none;
  border: none;
}

.backdrop-modal {
  opacity: 0;
  pointer-events: none;
  z-index: 99;
  background-color: #00000079;
  width: 100%;
  height: 100%;
  transition: opacity .7s ease-in-out, transform .7s ease-in-out;
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%)scale(.2);
}

.backdrop-modal:not(.hidden) {
  opacity: 1;
  pointer-events: auto;
  transform: translate(-50%, -50%)scale(1);
}

.backdrop-modal:not(.hidden) > body {
  overflow: none;
}

.hidden {
  opacity: 0;
  pointer-events: none;
  transform: translate(-50%, -50%), scale(.2);
  z-index: -100;
  transition: opacity .7s ease-in-out, transform .7s ease-in-out;
}

.onList {
  background-color: #ff001b !important;
}

.header {
  z-index: 99;
  background-image: url("header-background-mobile.a8ceff09.jpg");
  background-repeat: no-repeat;
  background-size: 100vw 100%;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 125px;
  font-size: .875rem;
  transition: all .3s ease-in-out;
  display: flex;
  position: fixed;
  top: 0;
}

.header__container {
  white-space: nowrap;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  transition: all .7s ease-in-out;
  display: flex;
}

.header__nav {
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  padding: 0 20px;
  transition: all .7s ease-in-out;
  display: flex;
}

.header__nav-container {
  justify-content: space-between;
  align-items: center;
  width: 100%;
  height: 100%;
  display: flex;
}

.header__nav-icon {
  stroke: #f5f5f5;
  width: 20px;
  height: 20px;
}

.header__nav-icon:hover {
  stroke: #ff6b08;
}

.header__nav-title {
  display: none;
}

.header__nav-list {
  justify-content: space-between;
  align-items: center;
  width: 100%;
  display: flex;
}

.header__nav-list-item {
  height: 21px;
}

.header__nav-list > ul {
  justify-content: center;
  align-items: center;
  gap: 20px;
  display: flex;
}

.header__nav-language-btn {
  color: #f5f5f5;
  text-transform: uppercase;
  border: 1px solid;
  border-radius: 5px;
  justify-content: center;
  align-items: center;
  padding: 2px;
  transition: all .3s ease-in-out;
  display: flex;
}

.header__nav-language-btn:hover {
  filter: drop-shadow(0 8px 43px #ff6b0199);
  background: #ff6b08;
}

.header__nav-form {
  border-bottom: 1px solid #ff6b08;
  justify-content: center;
  align-items: center;
  gap: 3px;
  width: 280px;
  margin-top: 25px;
  transition: all .9s ease-in-out;
  display: flex;
}

.header__nav-input {
  color: #f5f5f5;
  background: none;
  border: none;
  outline: none;
  width: 100%;
  padding: 4px;
}

.header__nav-input::placeholder {
  color: #f5f5f5;
}

.header__nav-input:focus + .header__nav-input-submit svg, .header__nav-input:hover + .header__nav-input-submit svg {
  color: #f5f5f5;
  stroke: #ff6b08;
}

.header__nav-input:focus::placeholder {
  color: #ff6b08;
}

.header__nav-input--loupe {
  cursor: pointer;
  stroke: #f5f5f5;
  transition: all .3s ease-in-out;
}

.header__error {
  color: #ff001b;
  text-align: center;
  white-space: nowrap;
  font-size: .75rem;
  display: none;
}

.header--library {
  background-image: url("my-library-mobile.f70bf307.jpg");
}

.header--library__container, .header--library__nav {
  height: 100%;
}

.header--library__btn--container {
  margin-bottom: 10px;
  transition: all .9s ease-in-out;
}

.header--library__btn--container > button {
  color: #f5f5f5;
  text-transform: uppercase;
  background: #00000079;
  border: 3px solid #ff6b08;
  border-radius: 5px;
  width: 130px;
  height: 44px;
  font-weight: 700;
  transition: all .3s ease-in-out;
}

.header--library__btn--container > button:hover {
  transform: scale(1.1);
}

.header--library__btn--container > button:first-of-type {
  margin-right: 15px;
}

.underline {
  border-bottom: 2px solid #ff6b08;
}

.header.scrolled {
  height: 40px;
  transition: all .7s ease-in-out;
}

.scrolled-container {
  height: 67px;
}

.hovered {
  transition: all .7s ease-in-out;
  height: 125px !important;
}

.form-transform {
  opacity: 0;
  pointer-events: none;
  z-index: -10;
  margin: 0;
  transition: all 10ms ease-in-out;
  transform: translateY(-300%);
}

.nav-height, .nav-height-library {
  padding-top: 25px;
  transition: all .7s ease-in-out;
  position: relative;
}

.buttons-transform {
  opacity: 0;
  pointer-events: none;
  z-index: -10;
  margin: 0;
  transition: all 10ms ease-in-out;
  transform: translateY(-300%);
}

.button-library--active {
  background: #ff6b08 !important;
}

@media screen and (width >= 768px) {
  .header {
    background-image: url("header-background-tablet.3b543a43.jpg");
    height: 108px;
  }

  .header__nav {
    font-size: 1rem;
  }

  .header__nav-title {
    display: block;
  }

  .header__nav-list {
    justify-content: space-between;
    align-items: center;
    width: 100%;
    display: flex;
  }

  .header__nav-list-item {
    height: auto;
  }

  .header__nav-icon {
    width: 26px;
    height: 26px;
  }

  .header__nav-link {
    justify-content: center;
    align-items: center;
    display: flex;
  }

  .header__nav-link:hover > svg {
    stroke: #ff6b08;
  }

  .header__nav-form {
    margin-top: 0;
    margin-bottom: 10px;
  }

  .header__nav-input--loupe {
    width: 16px;
    height: 16px;
  }

  .header-form {
    margin: 0;
  }

  .header--library {
    background-image: url("my-library-tablet.b7e58955.jpg");
  }

  .hovered {
    height: 108px !important;
  }

  .nav-height {
    padding-top: 40px;
  }

  .nav-height-library {
    padding-top: 7px;
  }
}

@media screen and (width >= 1280px) {
  .header__nav-form {
    width: 394px;
  }

  .header__error {
    width: 393px;
  }

  .header--library {
    background-image: url("my-library-desktop.2b600886.jpg");
  }
}

.library-films {
  justify-content: center;
  align-items: center;
  margin-top: 140px;
  display: flex;
}

.library-films__list {
  margin: 20px;
}

.library-films__list-item {
  cursor: pointer;
  border-radius: 5px;
  width: 100%;
  margin-bottom: 20px;
  transition: all .3s ease-in-out;
}

.library-films__list-item:hover {
  z-index: 10;
  transform: scale(1.1);
}

.library-films__list-item > img {
  border-radius: 5px;
  width: 100%;
  max-width: 320px;
  display: flex;
}

.library-films__list-item > h2 {
  text-transform: uppercase;
  margin-top: 5px;
  font-size: .75rem;
  line-height: 1.33;
}

.library-films__list-item > p {
  color: #ff6b08;
  font-size: .75rem;
  line-height: 1.17;
}

.library-films__list-item--rating {
  color: #f5f5f5;
  background-color: #ff6b08;
  border-radius: 5px;
  padding: 0 2px;
}

@media screen and (width >= 768px) {
  .library-films__list {
    flex-wrap: wrap;
    justify-content: center;
    gap: 32px;
    width: 704px;
    margin-top: 60px;
    display: flex;
  }

  .library-films__list-item, .library-films__list-item > img {
    max-width: 336px;
  }
}

@media screen and (width >= 1280px) {
  .library-films {
    width: 100%;
    max-width: 1280px;
    margin: 120px auto 20px;
  }

  .library-films__list {
    justify-content: center;
    gap: 16px;
    width: 1217px;
  }

  .library-films__list-item, .library-films__list-item > img {
    max-width: 395px;
  }
}

.films {
  justify-content: center;
  align-items: center;
  margin-top: 120px;
  display: flex;
}

.films__list {
  margin: 30px;
}

.films__list-item {
  cursor: pointer;
  border-radius: 5px;
  width: 100%;
  margin-bottom: 20px;
  transition: all .3s ease-in-out;
}

.films__list-item:hover {
  z-index: 10;
  transform: scale(1.1);
}

.films__list-item > img {
  border-radius: 5px;
  width: 100%;
  max-width: 320px;
  display: flex;
}

.films__list-item > h2 {
  text-transform: uppercase;
  margin-top: 5px;
  font-size: .75rem;
  line-height: 1.33;
}

.films__list-item > p {
  color: #ff6b08;
  font-size: .75rem;
  line-height: 1.17;
}

.films__list-item--rating {
  color: #f5f5f5;
  background-color: #ff6b08;
  border-radius: 5px;
  padding: 0 2px;
}

@media screen and (width >= 768px) {
  .films__list {
    flex-wrap: wrap;
    justify-content: center;
    gap: 32px;
    width: 704px;
    margin-top: 70px;
    display: flex;
  }

  .films__list-item, .films__list-item > img {
    max-width: 336px;
  }
}

@media screen and (width >= 1280px) {
  .films {
    width: 100%;
    max-width: 1280px;
    margin: 120px auto 20px;
  }

  .films__list {
    justify-content: center;
    gap: 16px;
    width: 1217px;
    margin: 70px 0 20px;
  }

  .films__list-item, .films__list-item > img {
    max-width: 395px;
  }
}

.footer {
  color: #f5f5f5;
  opacity: 0;
  text-align: center;
  z-index: 10;
  background-color: #1a181f;
  justify-content: center;
  align-items: center;
  width: 100%;
  padding: 2px 0;
  font-size: .75rem;
  line-height: 1;
  transition: all .7s ease-in-out;
  display: flex;
  position: fixed;
  bottom: 0;
  transform: translateY(100%);
}

.footer__svg {
  width: 10px;
  height: 10px;
}

.footer__link {
  color: #ff6b08;
  cursor: pointer;
  text-decoration: underline;
  transition: all .3s ease-in-out;
}

.footer__link:hover, .footer__link:focus {
  color: #050505;
  text-decoration: underline;
}

.footer:not(.hidden) {
  opacity: 1;
  transform: translateY(0);
}

@media screen and (width >= 768px) {
  .footer {
    font-size: .875rem;
  }
}

@media screen and (width >= 1280px) {
  .footer {
    font-size: 1.25rem;
  }

  .footer__svg {
    width: 16px;
    height: 16px;
  }
}

.details__container {
  z-index: 100;
  opacity: 0;
  pointer-events: none;
  border-radius: 10px;
  flex-wrap: wrap;
  width: 90vw;
  min-width: 300px;
  height: 85vh;
  padding: 7px;
  transition: opacity .7s ease-in-out, transform .7s ease-in-out;
  display: flex;
  position: absolute;
  top: 10%;
  left: 50%;
  overflow: auto;
  transform: translateX(-50%)scale(.8);
}

.details__container:not(.hidden) {
  opacity: 1;
  pointer-events: auto;
  transform: translate(-50%, -10%)scale(1);
}

@media (prefers-color-scheme: light) {
  .details__container {
    color: #050505;
    background-color: #f5f5f5;
  }
}

@media (prefers-color-scheme: dark) {
  .details__container {
    color: #f5f5f5;
    background-color: #161515;
  }
}

.details__close-button {
  border: 1px solid;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  width: 32px;
  height: 30px;
  transition: all .3s ease-in-out;
  display: flex;
  position: absolute;
  right: 15px;
}

.details__close-button:hover {
  border-color: #ff6b08;
}

.details__close-button:hover > svg {
  fill: #ff6b08;
}

@media (prefers-color-scheme: light) {
  .details__close-button > svg {
    fill: #000;
    stroke: #000;
  }
}

@media (prefers-color-scheme: dark) {
  .details__close-button > svg {
    fill: #fff;
    stroke: #fff;
  }
}

.details__img {
  border: .3rem solid #0003;
  border-radius: 12%;
  width: 100%;
  max-width: 95%;
  height: auto;
  max-height: 357px;
  margin: 40px auto 0;
}

.details__title {
  font-size: 1.25rem;
}

.details__information {
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
  display: flex;
}

.details__information-list {
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  gap: 6px;
  margin: 0 auto;
  list-style: none;
  display: flex;
}

.details__information-list--item {
  text-align: justify;
  justify-content: space-between;
  align-items: baseline;
  width: 100%;
  max-width: 280px;
  padding: 0 12px;
  display: flex;
  overflow: auto;
}

.details__information-list--item:last-of-type {
  max-width: 600px;
  padding: 0 12px;
}

.details__information-list--data {
  text-align: end;
  max-width: 120px;
}

.details__information-rating {
  background-color: #ff6b08;
  border-radius: 5px;
  justify-content: center;
  align-items: center;
  height: 16px;
  padding: 5px;
  display: inline-flex;
}

.details__about-container > h3 {
  text-align: center;
  margin-bottom: 8px;
}

.details__buttons {
  align-items: center;
  display: flex;
}

.btn-add-watched, .btn-add-queue {
  color: #f5f5f5;
  opacity: .8;
  background-color: #ff6b08;
  border-radius: 5px;
  justify-content: center;
  align-items: center;
  width: 110px;
  height: 38px;
  margin: 20px 0;
  font-size: .875rem;
  font-weight: 600;
  transition: transform .3s ease-in-out, opacity .3s ease-in-out;
  display: flex;
}

.btn-add-watched:hover, .btn-add-watched:focus, .btn-add-queue:hover, .btn-add-queue:focus {
  opacity: 1;
  transform: scale(1.1);
}

.btn-add-watched:first-of-type, .btn-add-queue:first-of-type {
  margin-right: 10px;
}

@media screen and (width >= 768px) {
  .details__container {
    flex-wrap: nowrap;
    justify-content: center;
    min-width: 704px;
    max-width: 900px;
    height: auto;
    top: 15%;
  }

  .details__img {
    object-fit: contain;
    width: auto;
    max-width: 45vw;
    height: auto;
    max-height: 90vh;
    margin: 40px 0 auto;
  }

  .details__title {
    margin-bottom: 20px;
  }

  .details__information {
    flex-wrap: wrap;
    margin-top: 5px;
    padding: 25px;
  }

  .details__about--p {
    max-height: 150px;
    margin-right: 8px;
  }

  .details__buttons {
    margin: 0;
  }

  .details__information-list--item:last-of-type {
    margin-left: 10px;
    padding: 0;
  }
}

@media screen and (width >= 1280px) {
  .details__img {
    max-width: 30vw;
  }
}

.login__container {
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: min(92vw, 360px);
  min-height: 240px;
  display: flex;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

@media (prefers-color-scheme: light) {
  .login__container {
    color: #050505;
    background-color: #f5f5f5;
  }
}

@media (prefers-color-scheme: dark) {
  .login__container {
    color: #f5f5f5;
    background-color: #161515;
  }
}

.login__container {
  border: 1px solid #ff6b0840;
  border-radius: 12px;
  padding: 20px;
  box-shadow: 0 10px 30px #00000040;
}

.login__form {
  flex-direction: column;
  gap: 12px;
  width: 100%;
  display: flex;
}

.login__label {
  width: 100%;
  display: block;
}

.login__input, .login--Password__input {
  border: 1px solid #ff6b0859;
  border-radius: 6px;
  outline: none;
  width: 100%;
  padding: 10px 12px;
  font-size: .875rem;
  transition: border-color .3s ease-in-out, box-shadow .3s ease-in-out;
}

@media (prefers-color-scheme: light) {
  .login__input, .login--Password__input {
    color: #050505;
    background-color: #fff;
  }
}

@media (prefers-color-scheme: dark) {
  .login__input, .login--Password__input {
    color: #f5f5f5;
    background-color: #ffffff0d;
  }
}

.login__input::placeholder, .login--Password__input::placeholder {
  opacity: .7;
}

.login__input:focus, .login--Password__input:focus {
  border-color: #ff6b08;
  box-shadow: 0 0 0 3px #ff6b0826;
}

.login__btn {
  color: #f5f5f5;
  text-transform: uppercase;
  background-color: #ff6b08;
  border: 1px solid #ff6b08;
  border-radius: 6px;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 42px;
  margin-top: 4px;
  font-size: .875rem;
  font-weight: 700;
  transition: transform .3s ease-in-out, box-shadow .3s ease-in-out, opacity .3s ease-in-out;
  display: flex;
}

.login__btn:hover, .login__btn:focus {
  opacity: .95;
  transform: scale(1.02);
  box-shadow: 0 8px 24px #ff6b084d;
}

.login__error {
  color: #ff001b;
  margin-top: 8px;
  font-size: .875rem;
  display: none;
}

@media screen and (width >= 768px) {
  .login__container {
    width: 420px;
    padding: 28px;
  }

  .login__form {
    gap: 14px;
  }
}

@media screen and (width >= 1280px) {
  .login__container {
    width: 460px;
    padding: 32px;
  }

  .films-watched {
    width: 100%;
    max-width: 1280px;
    margin: 0 auto;
  }

  .films-watched__list {
    gap: 16px;
    width: 1217px;
  }

  .films-watched__list-item {
    width: 395px;
    height: 634px;
  }

  .films-watched__list-item > img {
    width: 395px;
    height: 574px;
  }
}

.team__modal {
  z-index: 100;
  border-radius: 24px;
  flex-wrap: wrap;
  justify-content: center;
  max-width: 840px;
  height: 85vh;
  max-height: 558px;
  padding: 7px;
  display: flex;
  position: absolute;
  top: 10%;
  left: 50%;
  overflow: scroll;
  transform: translateX(-50%);
}

@media (prefers-color-scheme: light) {
  .team__modal {
    color: #050505;
    background-color: #f5f5f5;
  }
}

@media (prefers-color-scheme: dark) {
  .team__modal {
    color: #f5f5f5;
    background-color: #161515;
  }
}

.team__modal__container {
  margin-top: 40px;
}

.team__modal--close {
  fill: #403c3c;
  height: 35px;
  transition-duration: .25s;
  transition-property: fill border-color;
  background: none;
  border: none;
  width: 35px;
  padding: 4px;
  text-decoration: none;
  position: absolute;
  top: 20px;
  right: 20px;
}

.team__modal--close:hover, .team__modal--close:focus {
  fill: #ff6b08;
  transform: rotate(360deg);
}

@media (prefers-color-scheme: light) {
  .team__modal--close > svg {
    fill: #050505;
    stroke: #f5f5f5;
  }
}

@media (prefers-color-scheme: dark) {
  .team__modal--close > svg {
    fill: #f5f5f5;
    stroke: #161515;
  }
}

.team__modal--close > svg:hover, .team__modal--close > svg:focus {
  fill: #ff6b08;
  transform: rotate(360deg);
}

.team__title {
  text-align: center;
  margin: 20px auto 0;
  font-size: 1.25rem;
  line-height: 35px;
  transition: all .3s ease-in-out;
}

.team__title:hover, .team__title:focus {
  color: #ff6b08;
}

.team__title--note {
  text-align: center;
  max-width: 280px;
  margin: 0 auto 20px;
  font-size: .75rem;
}

.team__box {
  flex-wrap: wrap;
  justify-content: center;
  gap: 40px;
  display: flex;
}

.team__member {
  align-items: center;
  justify-content: baseline;
  text-align: center;
  flex-direction: column;
  width: 230px;
  margin: 0 auto;
